import React, { Component } from 'react';
import './App.css';

class App extends Component {
  displayHello = (str) => {
    return str;
  }

  render() {
    const msg = this.displayHello('Hello World...!');

    return (
      <div className="App">
        <br/><br/><br/>
        <h2>{msg}</h2>
      </div>
    );
  }
}

